*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --mainColor:#f57224;
  --whiteColor:#fff;
  --blackColor:#000;
  --lightDark:#222;

}
body{
  font-family: 'Lato',sans-serif;
}
nav{
  
  height: 100%;
  line-height: 70px;
 
  padding: 50px 10px ;
  box-sizing:0 1px 3px rgba(0,0,0,.1);
  display: flex;
  position: fixed;
  top: 0;
  background:#fff ;
  z-index: 9999;
  flex-direction: column;
}

nav ul{
  list-style: none;
  font-size: 10px;
  font-style: normal;
  color: #000;
  font-family: 'Lato',sans-serif;
}
nav ul a{
  text-decoration: none;
}


.bell{
  width: 100%;
  height: 50px;
  line-height: 70px;
  padding: 0 30px;
  box-sizing:0 1px 3px rgba(0,0,0,.1);
  display: flex;
  position: fixed;
  flex-direction: row;
  background:rgb(177, 233, 242) ;
  z-index: 9999;
  top: 0;
  
}
.left{
  margin-left: 1200px;
}
.right{
  padding-left: 10px;
}
.orders{
  width: 100%;
  height: 50px;
  line-height: 70px;
 margin-top: 100px;
  padding: 50px 10px ;
  box-sizing:0 1px 3px rgba(0,0,0,.1);
  display: flex;
  position: fixed;
  top: 0;
  background:#fff ;
  z-index: 9999;
  flex-direction: row;

}

.shoppingCart i{
  display: inline-block;
  font-size: 20px;
  color: var(--lightDark);
  margin-right: 15px;
}
.cartCourt{
  display: inline-block;
  width: 35px;
  height: 35px;
  background: var(--maincolor);
  color: var(--whiteColor);
  border-radius: 50%;
  line-height: 35px;
  text-align: center;
  font-size: 12px;

}
/*banneer*/
.container{
  width: 100%;
  padding: 0 100px;
}
header{
  width: 100%;
  background: url("./assets/bg3.jpg");
  height: 250px;
  margin-top: 70px;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(148, 214, 245);

}
.headerText{
  background: rgba(0,0,0,.2);
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.headerText h1{
  font-size: 30px;
  
}
.headerText p{
  font-size: 12px;
}
/*products*/

.products{
  display:flex;
  flex-wrap: wrap;
  width: 100%;
}

.product{
  width:25%;
  padding: 0 12px;
  margin: 20px 0;
  overflow: hidden;
  position: relative;
}

.hot{
  position: absolute;
  top: 0;
  background: rgb(240, 202, 12);
  padding: 5px 6px;
  color: var(--whiteColor);

}
.new
{
  position: absolute;
  top: 0;
  background: #05c52f;
  padding: 5px 6px;
  color: var(--whiteColor);
}
.product-image{
  width: 100%;
  height: 200px;
  overflow: hidden;
}
.product-image image{
  width: 100%;
}
.product-details
{
  padding: 15px 10px;
  text-align: center;
  background:var(--whiteColor);
  border:1px solid #fafafa;
}
.product-name{
  font-size: 20px;
  font-weight: 500;
  margin: 12px 0;
}
.product.price{
  font-size: 20px;
  font-weight: 300;
  margin:12px 0;

}
.add-to-cart{
  background: var(--mainColor);
  padding:10px 20px ;
  width: 100%;
  color: var(--whiteColor);
  text-transform: uppercase;
  text-align: center;
  font-size: 14px;

}
.not-found{
  margin-top: 100px;
  
}

.cart-container{
  width: 100%;
  margin-top: 70px;
  margin-left: 100px;
  padding: 0 100px;
  display:flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.cart-details
{
  width: 75%;
  border: 1px solid #fafafa;
  border-radius: 5px;

}
.cart{
  display: flex;
  align-items: center;
  background: var(--whiteColor);
  padding: 15px 20px;

}
.cart-image{
  display: inline-block;
  width: 120px;
  height: 120px;
  border-radius: 5px;
  overflow:hidden;

}
.cart-image img {
  width: 100%;
  height: 100%;
}

.cart-product-name{
  width: 120px;
  padding: 0 15px;
  font-weight: bold;
  font-size: 14px;
}
.cart-product-price{
  width:120px;
  padding: 0 15px;
  display:inline-block;
  text-align: center;
  font-weight: bold;
  color:var(--mainColor);

}
.inc{
  display:inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: 1px solid var(--mainColor);
  font-size: 14px;
  color: var(--mainColor);
  border-radius: 3px;
  cursor:pointer;

}
.product-quantity{
  display: inline-block;
  padding: 0 15px;
  font-size: 15px;
  width: 70px;
  text-align: center;

}
.dec{
  display:inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: 1px solid rgba(238, 50, 50, 0.1);
  font-size: 14px;
  color: var(--mainColor);
  border-radius: 3px;
  cursor:pointer;

}
.product-total-price{
  display: inline-block;
  width: 170px;
  padding: 0 15px;
  text-align: center;
}
.delete-product{
  border:none;
  color: rgb(246, 175, 23);
  cursor:pointer;
}

.cart-summary{
  width:25%;
  padding: 0 0 0 20px ;
margin-top: 80px;
}
.summary{
  background: var(--whiteColor);
  padding: 13px;

}
.summary h3{
  font-size:18px;
  font-weight:normal;
  border-bottom:1px solid #999;
  padding-top: 8px;
}
.total-items{
  display:flex;
  margin:15px 0;

}
.items{
  width:50%;
  text-align: left;
  font-weight: normal;
}
.items-count{
  width: 50%;
  text-align: right;
  font-weight:bold;
  color:var(--mainColor)
}
.total-price-section {
  display:flex;
  margin:15px 0;

}
.just-title{
  width: 50%;
  text-align: left;
  font-weight: normal;

}
.items-price{
  width: 50%;
  text-align: right;
  font-weight:bold ;
  color:var(--mainColor);
}
.empty{
  padding: 10px 20px;

}
@media(max-width:765px){
  .headerText{
    padding: 0 15px;

  }
  .headerText h1{
    font-size: 20px;
  }
  .products{
    flex-direction: column;
  }
  .product{
    width: 100%;
  }
  .cart{
    flex-direction: column;
  }
  .cart-container{
    flex-direction: column;
  }
 .cart-details{
   width:100%;
 }  
 .cart-image{
   padding: 15px 0;
 }
 .cart-product-name{
   padding:15px 0;
   text-align: center;
 }
 .cart-product-price{
   padding:15px 0;
 }
 .product-quantity{
   padding: 15px 0;
 }
 .product-total-price{
   padding: 15px 0;
 }
 .cart-summary{
   width: 100%;
 }
}